    import React, { useState, useEffect } from 'react';
    import { Link, useNavigate } from 'react-router-dom';
    import styles from './AdminHeader.module.css'; // Импортируем стили из модуля
    import axios from 'axios';

    export default function AdminHeader({ children }) {
      const navigate = useNavigate(); // Для редиректа
      const [loading, setLoading] = useState(false); // Состояние для загрузки

      // Загружаем токен при загрузке компонента
      const token = localStorage.getItem('token_admin'); // Получаем токен из localStorage

      // Обработчик выхода (logout)
      const handleLogout = async () => {
        if (!token) {
          alert('Ошибка: не найден токен. Пожалуйста, войдите заново.');
          return;
        }

        setLoading(true);
        try {
          const response = await axios.post('/api/admin/logout', {}, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });

          if (response.status === 200) {
            localStorage.removeItem('token_admin');
            navigate('/auth/admin');
          } else {
            alert('Не удалось выйти. Попробуйте позже.');
          }
        } catch (error) {
          console.error('Ошибка при выходе:', error);
          alert('Ошибка при выходе. Попробуйте позже.');
        } finally {
          setLoading(false);
        }
      };

      return (
        <div className={styles.adminPanel}>
          <div className={styles.sidebar}>
            <div className={styles.logo}>
            <Link to={'/admin'}>
                        <img src="/images/erudit.svg" alt="erudit_logo" />
                    </Link>
            </div>
            <Link to="/admin/questions" className={styles.sidebarBtn}>База вопросов</Link>
            <Link to="/admin/users" className={styles.sidebarBtn}>Пользователи</Link>
            <Link to="/admin/ratings" className={styles.sidebarBtn}>Топ участников</Link>

            <div className={styles.user}>
                <Link to="/" className={styles.userLink}>Войти как пользователь</Link>

            </div>

            {/* Кнопка Logout */}
            {token ? (
                <div className={styles.logBtn}>
                                <button
                className={styles.logoutBtn}
                onClick={handleLogout}
                disabled={loading}
              >
                {loading ? 'Выход...' : 'Выйти'}
              </button>
                </div>
            ) : (
              <div className={styles.loginReminder}>
                <Link to="/auth/admin">Войдите снова</Link>
              </div>
            )}
          </div>
          <div className={styles.content}>
              {children}
          </div>

        </div>
      );
    }
