import { useState } from "react";
import axios from "axios";
import { useNavigate, Link } from "react-router-dom";
import styles from './Login.module.css';  // Подключаем модуль CSS
import Header from "../../../components/Header";

export default function AdminLogin() {
  const [data, setData] = useState({ username: "", password: "" });
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  const navigate = useNavigate();

  // Обработчик изменения значений в полях ввода
  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({
      ...data,
      [name]: value,
    });
  };

  // Обработчик отправки формы
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null); // Сброс ошибок перед отправкой
    setSuccessMessage(null); // Сброс сообщения об успехе

    try {
      const response = await axios.post("/api/admin/login", data);

      if (response.status === 200 && response.data.token_admin) {
        const token = response.data.token_admin;
        localStorage.setItem("token_admin", token);  // Сохраняем токен в localStorage
        setSuccessMessage("Вход выполнен успешно!");  // Уведомление об успешном входе
        navigate('/admin'); // Перенаправление на страницу администрирования
      } else {
        // Если по какой-то причине токен не был возвращен
        setError("Неверные данные для входа.");
      }
    } catch (error) {
      if (error.response) {
        // Ошибка от сервера (например, неверный логин или пароль)
        if (error.response.status === 401) {
          setError("Неверный логин или пароль.");
        } else {
          setError("Произошла ошибка при авторизации.");
        }
      } else {
        // Ошибка сети или другая
        setError("Ошибка соединения с сервером.");
      }
    }
  };

  return (
    <>
      <Header />
      <div className={styles.loginForm}>
        <p className={styles.loginForm_title}>Вход для админа</p>

        {/* Показываем сообщение об ошибке */}
        {error && <div className={styles.errorMessage}>{error}</div>}

        {/* Показываем сообщение об успехе */}
        {successMessage && <div className={styles.successMessage}>{successMessage}</div>}

        <form onSubmit={handleSubmit}>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="username"
              value={data.username}
              onChange={handleChange}
              placeholder="Логин: "
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="password"
              name="password"
              value={data.password}
              onChange={handleChange}
              placeholder="Пароль: "
              required
            />
          </div>
          <div>
            <button type="submit" className={styles.submitButton}>Войти</button>
          </div>
        </form>

        {/* Перенаправление на главную страницу */}
        <div className={styles.back}>
          <Link className={styles.backNav} to={"/"}>› назад</Link>
        </div>
      </div>
      <div className={styles.light2}>
        <img src="/images/light2_image.png" alt="light2_image" />
      </div>
    </>
  );
}
