import { useState, useEffect } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import styles from './Login.module.css';
import Header from "../../../components/Header";

export default function Login() {
  const [data, setData] = useState({ username: "", password: "" });
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  // Обработчик изменения значений в полях ввода
  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({
      ...data,
      [name]: value,
    });

    // Clear error message when the user starts typing
    if (error) {
      setError(null);
    }
  };

  // Обработчик отправки формы
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null); // Сброс ошибок перед отправкой
    setSuccessMessage(null); // Сброс сообщения об успехе
    setLoading(true); // Включаем индикатор загрузки

    try {
      const response = await axios.post("/api/login", data);

      if (response.status === 200 && response.data.token) {
        const token = response.data.token;
        localStorage.setItem("token", token);  // Сохраняем токен в localStorage
        setSuccessMessage("Вход выполнен успешно!");  // Уведомление об успешном входе
      } else {
        setError("Неверные данные для входа.");
      }
    } catch (error) {
      if (error.response) {
        if (error.response.status === 401) {
          setError("Неверный логин или пароль.");
        } else {
          setError("Произошла ошибка при авторизации.");
        }
      } else {
        setError("Ошибка соединения с сервером.");
      }
    } finally {
      setLoading(false); // Выключаем индикатор загрузки
    }
  };

  useEffect(() => {
    if (successMessage) {
      navigate('/profile');
    }
  }, [successMessage, navigate]);


  return (
    <>
      <Header />
      <div className={styles.loginForm}>
        <p className={styles.loginForm_title}>Вход в систему</p>

        {/* Показываем сообщение об ошибке */}
        {error && <div className={styles.errorMessage}>{error}</div>}

        {/* Показываем сообщение об успехе */}
        {successMessage && <div className={styles.successMessage}>{successMessage}</div>}

        <form onSubmit={handleSubmit}>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="username"
              value={data.username}
              onChange={handleChange}
              placeholder="Логин: "
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="password"
              name="password"
              value={data.password}
              onChange={handleChange}
              placeholder="Пароль: "
              required
            />
          </div>
          <div>
            <button type="submit" className={styles.submitButton} disabled={loading}>
              {loading ? 'Загружаем...' : 'Войти'}
            </button>
          </div>
          <div className={styles.back}>
            <Link className={styles.backNav} to={"/"}>› назад</Link>
          </div>
          <div className={styles.reg_nav}>
            <p className={styles.reg_nav_text}>Нет аккаунта? <Link to={'/auth/register'}>Регистрация</Link></p>
          </div>
        </form>
      </div>

    </>
  );
}
