import React, { useState, useEffect } from 'react';
import axios from 'axios';
import AdminHeader from '../../components/AdminHeader/AdminHeader';
import styles from './AdminPanel.module.css'; // Импортируем стили

export default function AdminPanel() {
  const [scorersCount, setScorersCount] = useState(0);
  const [loading, setLoading] = useState(false); // Состояние для загрузки
  const [error, setError] = useState(null); // Состояние для обработки ошибок

  // Получаем общее количество участников
  const fetchScorersCount = async () => {
    const token = localStorage.getItem("token_admin"); // Получаем токен из localStorage
    if (!token) {
      setError("Токен не найден");
      return;
    }

    setLoading(true); // Включаем индикатор загрузки
    try {
      const response = await axios.get('/api/admin/scorers-count', {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      setScorersCount(response.data.total_scorers);
    } catch (error) {
      setError("Ошибка при получении данных");
      console.error("Ошибка:", error);
    } finally {
      setLoading(false); // Выключаем индикатор загрузки
    }
  };

  useEffect(() => {
    fetchScorersCount();
  }, []);

  return (
      <AdminHeader>
              <div className={styles.content}>
                {/* Проверяем, если идет загрузка или ошибка */}
                {loading && <p>Загрузка...</p>}
                {error && <p className={styles.error}>{error}</p>}

                {/* Отображаем количество участников */}
                {!loading && !error && (
                  <div className={styles.scorersCount}>
                    <h2>Общее количество участников: {scorersCount}</h2>
                  </div>
                )}
      </div>
      </AdminHeader>
  );
}
